import { useEffect, useRef } from "react";
import BackBtn from "../../Components/UI/BackBtn/BackBtn";
import classes from "./LMSUser.module.css";
import telephone from "../../assets/icons/telephone.svg";
import whatsapp from "../../assets/icons/whatsapp.svg";
import CustomButton from "../../Components/UI/Button/CustomButton";
import { useToastMessage } from "../../contexts/ToastMessageContext";
import { leadColor } from "../../Shared/Utility";
import { useLead } from "../../contexts/LMSDashboard/LeadContext";

const LMSUser = (props) => {
  const toastMessage = useToastMessage();

  const { currentLeadId, getFullLeadData, currentLeadData } = useLead();

  const getFullLeadDataRef = useRef(() => {
    (!currentLeadId || currentLeadId !== props.match.params.leadId) &&
      getFullLeadData(props.match.params.leadId, toastMessage);
  });

  useEffect(() => {
    getFullLeadDataRef.current();
  }, []);

  const leadData = currentLeadData;

  return (
    <section>
      <div className={classes.CustomButton}>
        <CustomButton onClick={() => props.history.goBack()}>
          Close the lead
        </CustomButton>
      </div>
      <div className={classes.Modal}>
        {leadData && (
          <div className={classes.Header_Profile}>
            <span
              style={{ backgroundColor: leadColor(leadData.status) }}
            ></span>
            <p style={{ color: leadColor(leadData.status) }}>
              {leadData.status}
            </p>
          </div>
        )}

        <header style={{ marginTop: "1%" }} className={classes.Modal2}>
          <BackBtn />
          <div className={classes.Modal_Title}>
            <h1>
              {(leadData && leadData.lead_data.name) ||
                (leadData && leadData.contact_number)}
            </h1>
          </div>
          <div className={classes.Call_Btn}>
            {leadData && (
              <a
                target="_blank"
                rel="noopener noreferrer"
                href={`https://api.whatsapp.com/send?phone=${leadData.contact_number}`}
              >
                <img src={whatsapp} alt="call" />
              </a>
            )}
          </div>
          <div className={classes.Call_Btn}>
            {leadData && (
              <a href={`tel:${leadData.contact_number}`}>
                <img src={telephone} alt="call" />
              </a>
            )}
          </div>
        </header>
      </div>
      <main className={classes.LMSUser_Main}>
        <div className={classes.LMSUser_Main_Top}>
          {/* {console.log(
            leadData && leadData.currentStatus.replace(/\s+/g, " ").trim()
          )} */}

          {leadData && (
            <div className={classes.Lead_Details}>
              <ul>
                {Object.entries(leadData.lead_data).map((e, i) => {
                  if (e[0] === "created_at") {
                    return (
                      <li key={i}>
                        <p className={classes.KEY}>
                          {e[0].charAt(0).toUpperCase() + e[0].substring(1)}:
                        </p>{" "}
                        <p className={classes.VALUE}>
                          {new Date(e[1]).toLocaleString()}
                        </p>
                      </li>
                    );
                  }
                  return (
                    <li key={i}>
                      <p className={classes.KEY}>
                        {e[0].charAt(0).toUpperCase() + e[0].substring(1)}:
                      </p>{" "}
                      <p className={classes.VALUE}>{e[1]}</p>
                    </li>
                  );
                })}
              </ul>
              {/* <Link to="#edit">Edit details</Link> */}
            </div>
          )}
        </div>
      </main>

      <div className={classes.Alert}>
        <p>More features are available on Desktop.</p>
      </div>
      {/* <UserResponsePopup
        setOpen={setOpen}
        open={state.showUserResponsePopup}
        selectedLMSLead={state.selectedLead}
      /> */}
    </section>
  );
};

export default LMSUser;
